import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/views/Home';
import Games from '@/views/Games';
import Art from '@/views/Art';
import About from '@/views/About';
import DnD from '@/views/DnD';
import Blog from '@/views/Blog';
import Post from '@/components/Post';
import './../lib/animatedHeader.js';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dd-home',
      component: Home
    },
    {
      path: '/games',
      name: 'dd-games',
      component: Games
    },
    {
      path: '/art',
      name: 'dd-art',
      component: Art
    },
    {
      path: '/about',
      name: 'dd-about',
      component: About
    },
    {
      path: '/blog',
      name: 'dd-blog',
      component: Blog
    },
    {
      path: '/dnd',
      name: 'dd-dnd',
      component: DnD
    },
    {
      path: '/post/:id',
      name: 'dd-post',
      component: Post,
      props: true
    }
  ]
})



