<template>
  <div id="app">
    <dd-header />
    <div id="parallax" class="rellax">
      <div class="spacer"></div>
    </div>
    <div id="parallaxFast" class="rellax2">
      <h1>Web and Games development</h1>
      <p>I'm available for freelance Web, Unity and Pixel Art work, send a message and let's chat!</p>
      <button>Contact me!</button>
    </div>
    <div id="container">
      <div id="content">
        <router-view></router-view>
      </div>
      <dd-sidebar />
    </div>

    <dd-footer />
  </div>
</template>


<script>
import DDheader from "@/components/Header";
import DDfooter from "@/components/Footer";
import DDsidebar from "@/components/Sidebar";
import Rellax from "@./../rellax";

export default {
  name: "App",
  components: {
    "dd-header": DDheader,
    "dd-footer": DDfooter,
    "dd-sidebar": DDsidebar
  },
  mounted() {
    var rellax = new Rellax(".rellax", {
      speed: -7,
      center: false,
      wrapper: null,
      round: true,
      vertical: true,
      horizontal: false
    });

    var rellax2 = new Rellax(".rellax2", {
      speed: 10,
      center: false,
      wrapper: null,
      round: true,
      vertical: true,
      horizontal: false
    });
  }
};
</script>

<style>
@import url("https://fonts.googleapis.com/css?family=Exo:400,800|Orbitron|Rajdhani&display=swap");

.spacer {
  padding-top: 65vh;
  height: 30vh;
  width: 100%;
  background: rgb(35, 31, 32);
  background: linear-gradient(
    175deg,
    rgba(35, 31, 32, 1) 0%,
    rgba(35, 31, 32, 1) 35%,
    rgba(35, 31, 32, 0.4) 100%
  );
}

#parallax {
  background-image: url(assets/backgr.jpg);
  background-position: 0px -150px;
  z-index: -1;
  background-size: 100vw;
}

#parallaxFast {
  width: 50%;
  height: 25%;
  position: absolute;
  left: 35%;
  right: 15%;
  top: 15%;
  color: white;
  /* background-color: rgba(0, 0, 0, 0.75); */
}

#parallaxFast h1 {
  font-size: 64px;
  text-align: center;
  font-family: "Orbitron", sans-serif;
}

#parallaxFast p {
  font-size: 24px;
  text-align: center;
  font-family: "Orbitron", sans-serif;
}

#app {
  font-family: "Exo", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background: transparent;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  /* background-image: url(assets/backgr.jpg); */
}
#container {
  width: 100%;
  background-color: white;
  padding: 30px;
  -webkit-box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.75);
  display: flex;
}

#content {
  width: 75%;
}

@media screen and (max-width: 1024px) {
  #container {
    width: 100%;
    background-color: white;
    padding: 30px;
    -webkit-box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.75);
    flex-direction: column;
  }

  #content {
    width: 100%;
  }

  #parallaxFast h1 {
    font-size: 28px;
    text-align: center;
    font-family: "Orbitron", sans-serif;
  }

  #parallaxFast p {
    font-size: 16px;
    text-align: center;
    font-family: "Orbitron", sans-serif;
  }

  #parallaxFast {
    width: 50%;
    height: 25%;
    position: absolute;
    left: 25%;
    right: 25%;
    top: 20%;
    color: white;
    /* background-color: rgba(0, 0, 0, 0.75); */
  }
}

/*GLOBAL CSS*/

button {
  background-color: #f4991f;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  flex-direction: column;

  font-size: 16px;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
}

button:hover {
  background-color: #f75b13;
  color: white;
}

h1 {
  text-align: left;
}

a {
  color: #f4991f;
  text-decoration: none;
}

a:hover {
  color: #f75b13;
}

#pageIntroduction {
  width: 90%;
  margin: auto;
  padding-top: 40px;
}

.postDivider {
  border-bottom: grey solid 1px;
  margin: auto;
  width: 90%;
  padding: 20px;
  margin-bottom: 30px;
}

* {
  padding: 0;
  margin: 0;
}
html,
body {
  height: 100%;
  display: flex;
  flex-direction: column;
}
body > * {
  flex-shrink: 0;
}

@media screen and (max-width: 1024px) {
  h1 {
    text-align: left;
    font-size: 32px;
  }

  h2 {
    text-align: left;
    font-size: 24px;
  }

  h3 {
    text-align: left;
    font-size: 16px;
  }

  p {
    font-size: 14px;
  }
}
</style>



